.NavBar{
    button{
        border: none;
        width: fit-content;
        height: fit-content;
    }

    .w100{
        width: 100%;
    }

    ul, .left, .right, ul li{
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
    }

    .left div, .right div{
        font-size: .8rem;
        color: #e1e1e1;
        font-family: 'josefinsans-regular';
    }
    
    ul{
        height: 1.4rem;
        justify-content: space-between;
        padding: 0 1.2rem 0 1.2rem;
        background-color: #23232e;
    }

    .left button{
        margin-right: 1rem;
    }

    .right button {
        margin-left: 1rem;
    }
}